.start-page {
    margin-top: auto;
    align-items: center;
    text-align: center;
}
.quiz-name {
    margin-top: auto;
    display: flex;
    width: 700px;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    border:solid 2px gray;
    border-radius: 20px;
    margin-bottom: auto;
    font-size: 2rem;
}
.quiz-question {
    display: flex;
    justify-content: space-between;
}
.start-img {
    width: 700px;
}
@media screen and (max-width: 768px) { 
    .start-img {
        width: 80%;
    }
    .quiz-name {
        margin-top: auto;
        display: flex;
        width: 80%;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
        border:solid 2px gray;
        border-radius: 20px;
        margin-bottom: auto;
        font-size: 2rem;
    }
    .result-display {
        align-items: center;
        text-align: center;
        margin: 0 auto;
    }
    
}

.result-display {
    align-items: center;
    text-align: center;
    margin: 0 auto;
}

.leftbutton {
    width: 50%;
}

.rightbutton {
    width: 50%;
}